.button {
  display: inline-flex;
  height: 40px;
  width: 125px;
  border: 2px solid #BFC0C0;
  margin: 20px 20px 20px 20px;
  background-color: #2D3142;
  color: #BFC0C0;
  text-transform: uppercase;
  text-decoration: none;
  font-size: .8em;
  letter-spacing: 1.5px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

#button-2 {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

#button-2 a {
  position: relative;
  -webkit-transition: all .35s ease-Out;
  transition: all .35s ease-Out;
}

#slide {
  width: 100%;
  height: 100%;
  left: -200px;
  background: #BFC0C0;
  position: absolute;
  -webkit-transition: all .35s ease-Out;
  transition: all .35s ease-Out;
  bottom: 0;
}

#button-2:hover #slide {
  left: 0;
}

#button-2:hover a{
  color: #2D3142;
}

