.node {
	width: 35px;
	height: 35px;
  outline: 1px solid rgb(179, 204, 255);
	display: inline-block;
  vertical-align: top;
}

.nodefinish {
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(175,216,248);
	display: inline-block;
  background-image: url("logo/new_end.png");
  background-size: contain;
  vertical-align: top;
}

.nodestart {
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(175,216,248);
	display: inline-block;
 	background-image: url("logo/start.png");
  background-size: contain;
  vertical-align: top;

}

.nodewall {
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(175,216,248);
	display: inline-block;
 	background-color: rgb(12, 53, 71);
  vertical-align: top;
}

.red{
  background-image: url("logo/5.png");
  background-size: contain;
}

.green{
  background-image: url("logo/100.png");
  background-size: contain;
}

.blue{
  background-image: url("logo/25.png");
  background-size: contain;
}

.final{
  background-color: rgb(255, 254, 106);
}

.otherwise{
  background-color: rgba(0, 190, 218, 0.75)
}

.otherwise-visited{
  background-image: url("logo/small.png");
  background-size: contain;
  background-position: center;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;

}


@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-image: url("logo/current.png");
    background-size: contain;
    background-position: center;
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);

  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    /*transform: scale(1);
    background-color: var(--v,"white");*/
  }
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}


@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
    background-image: url("logo/dot.png");
    background-size: contain;
    background-position: center;
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
    background-image: url("logo/dot.png");
    background-size: contain;
    background-position: center;
  }

