#radios{
  position:relative;
  width:23%;
  margin:auto;
}
input[type="radio"]{ 
  position:absolute;
  right:1000%;
}
label{
  float:left;
  width:17%; padding-bottom:17%;
  margin-top: 0%;
  margin-bottom: 0%;
  margin-left: 7.5%;
  background:rgb(255,255,255);
  border-radius:50%;
  cursor:pointer;
}

#for5{
  background-image: url("images/5.png");
  background-size: contain;
}
