.footer {
  position: fixed;
  left: 0;
  bottom: 0px;
  width: 100%;
  height: 8%;
  background-color: blue;
  display: flex;
  flex-direction: row;
}

.footer-text{
	margin-right: 15px;
}

.container-info {
  display: inline-flex;
  height: 80%;
  width: 10%;
  margin-left: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 20px;
  color: #BFC0C0;
  text-decoration: none;
  font-size: .8em;
  letter-spacing: 1.5px;
  text-align: center;
  align-items: center;
  overflow: hidden;
}

.start{
	background-image: url("images/start.png");
	background-position: right;
	background-size: cover;
}
.end{
	background-image: url("images/end.png");
	background-position: right;
	background-size: cover;
}
.unvisited{
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(179, 204, 255);
	background-color: white
}
.visited{
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(179, 204, 255);
	background-color: rgba(0, 190, 218, 0.75)
}

.current{
	width: 35px;
	height: 35px;
	outline: 1px solid rgb(179, 204, 255);
	background-color: rgba(0, 190, 218, 0.75);
	background-image: url("images/current.png");
	background-size: cover;
	background-position: center;
}

.path{
	width: 35px;
